<template>
  <div
    @keydown="onKeyDown($event)"
    @keyup="onKeyUp($event)"
    style="padding: 10px"
    id="app"
  >
    <a-row :gutter="4" type="flex" justify="center">
      <a-col :md="6" :lg="6" :xl="6" :xxl="4">
        <a-card style="height: 380px">
          <SelectionPanel
            ref="selectionPanel"
            :robotList="robotList"
            :heading="heading"
            :status="status"
            :collidedWith="collidedWith"
            @robotChange="handleRobotChange"
            @modeSelection="emitModeSelectionEvt"
          />
        </a-card>
      </a-col>


      <a-col :md="7" :lg="7" :xl="7" :xxl="7">
        <a-card style="height: 380px">
          <NavigationPanel
            @manualMode="handleManualMode"
            @mouseUp="handleMouseUp"
            ref="navigationPanel"
          />
        </a-card>
      </a-col>
      <a-col :md="8" :lg="8" :xl="8" :xxl="6">
        <a-card style="height: 380px">
          <ObjectHandlingPanel
            @manualMode="handleManualMode"
            @mouseUp="handleMouseUp"
            ref="objectHandlingPanel"
          />
        </a-card>
      </a-col>
    </a-row>

    <a-row :gutter="4" style="margin-top: 5px" type="flex" justify="center">
      <a-col :md="13" :lg="13" :xl="13" :xxl="11">
        <iframe
          src="https://player.twitch.tv/?channel=bachng248"
          height="100%"
          width="100%"
        ></iframe>
      </a-col>
      <a-col :md="8" :lg="8" :xl="8" :xxl="6">
        <a-card style="height: 380px">
          <MapControlPanel
            ref="mapControlPanel"
            @packageClick="emitPackageEvt"
          />
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import SelectionPanel from "./components/SelectionPanel";
import NavigationPanel from "./components/NavigationPanel";
import ObjectHandlingPanel from "./components/ObjectHandlingPanel";
import LightControlPanel from "./components/LightControlPanel";
import AVControlPanel from "./components/AVControlPanel";
import MapControlPanel from "./components/MapControlPanel";
export default {
  name: "App",
  components: {
    SelectionPanel,
    NavigationPanel,
    ObjectHandlingPanel,
    LightControlPanel,
    AVControlPanel,
    MapControlPanel,
  },
  data() {
    return {
      camPlaying: false,
      robotList: [],
      heading: null,
      status: null,
      collidedWith: null,
    };
  },
  sockets: {
    connect() {
      console.log("Socket connected!");
      this.$socket.emit("gui_join");
    },
    clients_list(data) {
      console.log("client list", data);
      this.robotList = data;
    },
    gui_join(data) {
      console.log("GUI joined successfully with client list", data);
      this.robotList = data;
    },
    change_heading(data) {
      console.log("Change heading", data);
      this.heading = data;
    },
    status(data) {
      console.log("Status evt", data);
      this.status = data.status;
      this.collidedWith = data.collided_with;
    },
  },
  methods: {
    stopCam() {
      this.$refs.webcam.stop();
    },
    startCam() {
      this.$refs.webcam.start();
    },
    pauseCam() {
      this.$refs.webcam.pause();
    },
    resumeCam() {
      this.$refs.webcam.resume();
    },
    handleError(err) {
      console.log("err", err);
    },
    logCams(cams) {
      //console.log("cams", cams);
    },
    handleRobotChange(robot) {
      this.$refs.navigationPanel.selectedRobot = robot;
      this.$refs.objectHandlingPanel.selectedRobot = robot;
      this.$refs.mapControlPanel.selectedRobot = robot;
    },
    handleManualMode() {
      this.$refs.selectionPanel.mode = "Manual";
    },
    handleMouseUp(payload) {
      console.log("emitting stop evt");
      this.$socket.emit("manual", payload);
    },
    emitPackageEvt(payload) {
      console.log("emitting pick_up evt, payload", payload);
      this.$socket.emit("pick_up", payload);
    },
    emitModeSelectionEvt(payload) {
      this.$refs.mapControlPanel.mode = payload.mode;
      console.log("emitting mode evt, payload", payload);
      this.$socket.emit("mode", payload);
    },
    onKeyDown(evt) {
      const me = this,
        navigationPanel = me.$refs.navigationPanel,
        objectHandlingPanel = me.$refs.objectHandlingPanel;
      switch (evt.keyCode) {
        case 38:
          // ArrowUp
          navigationPanel.handleNavigation("forward");
          break;
        case 40:
          // ArrowDown
          navigationPanel.handleNavigation("down");
          break;
        case 39:
          // ArrowRight
          navigationPanel.handleNavigation("right");
          break;
        case 37:
          // ArrowLeft
          navigationPanel.handleNavigation("left");
          break;
        case 80:
          // Pickup P
          objectHandlingPanel.handleObject("pick_up");
          break;
        case 68:
          // Drop D
          objectHandlingPanel.handleObject("drop");
          break;
      }
    },
    onKeyUp(evt) {
      const me = this,
        navigationPanel = me.$refs.navigationPanel,
        objectHandlingPanel = me.$refs.objectHandlingPanel;
      switch (evt.keyCode) {
        case 38:
        // ArrowUp
        case 40:
        // ArrowDown
        case 39:
        // ArrowRight
        case 37:
          // ArrowLeft
          navigationPanel.handleMouseUp();
          break;
        case 80:
        // Pickup P
        case 68:
          // Drop D
          objectHandlingPanel.handleMouseUp();
          break;
      }
    },
  },
};
</script>

<style scoped>
#app {
  height: 100vh;
}</style
>>
